.about__container{
    display: grid;
    grid-template-columns: 35% 50%;;
    gap:15%;
}

.about__me{
    width:100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background:linear-gradient(
        45deg,
        transparent,
        var(--color-primary),
        transparent
    );
    display: grid;
    place-items: center;
}

.about__me-image{
    border-radius: 2rem;
    overflow: hidden;
    width: 14rem;
    transform: rotate(10deg);
    transition: var(--transition);
    background: linear-gradient( var(--color-white), transparent);
}

.about__me-image:hover{
    transform:rotate(0);
}
.about__cards{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap:1.5rem;
}
.about__card{
    background:var(--color-white);
    border:1px solid transparent;
    border-radius: 1rem;
    padding:2rem;
    color:var(--color-primary);
    text-align: center; 
    transition: var(--transition);
    margin-bottom: 2rem;
}
.about__card h5{
    font-size: 0.95rem;
}
.about__card:hover{
    background:transparent;
    color:#fff !important;
    border-color:var(--color-white);
    cursor:default

}
.about__icon{

    font-size: 1.4rem;
    margin-bottom: 1rem;
}
.about__card small{
    font-size:0.7rem;

}
.bout_content p{
    margin:2rem 0 2.6rem;
}

/*===============Tablet Device ================*/
 @media screen and (max-width: 1024px){
    .about__container{
        grid-template-columns: 1fr;
        gap:0;
    }
    .about__me{
        width:50%;
        margin: 2rem auto 4rem;
    }
    .about__content p{
        margin: 1rem 0 1.5rem;
    }
 }




/* ====================Mobile Device ===============*/
@media  screen and (max-width:600px) {
    .about__me{
        width:65px;
        margin:0 auto 3rem;
    }
    .about__cards{
        grid-template-columns: 1fr 1fr;
        gap:1rem
    }
    .about__content{
        text-align: center;
    }
    .about__content p{
        margin:1.5rem 0;
    }

}