footer{
    background-color: var(--color-primary);
    padding:3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top:7rem;
}

footer a {
    color: var(--color-white);
}

.footer__logo{
    font-size:2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}

ul.permalinks{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap:2rem;
    margin: 0 auto 3rem;
}

.footer__socials{
    display: flex;
    justify-content:center;
    gap:1rem;
    margin-bottom:4rem;
}

.footer__socials a{
    background:var(--color-white);
    color:var(--color-primary);
    padding:0.8rem;
    border-radius:0.7rem;
    display: flex;
    border:1px solid transparent;
}
.footer__socials a:hover{
    background:transparent;
    color:var(--color-white);
    border-color:var(--color-white);
}
.footer__copyright{
    margin-bottom:4rem;
    color:#c2c2c2;
}
/*=================Mobile Display===================*/
@media screen and (max-width:600px){
   .footer__socials{
    margin-bottom: 2.6rem;
   }
}